@import url("https://fonts.google.com/specimen/Space+Grotesk");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

#root {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 8px;
    background: rgb(242, 242, 242);
}

.App {
    border-radius: 10px;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
    width: 1440px;

    display: flex;
    height: 90vh;
    background: #fff;

    .left_bar {
        border-radius: 10px;

        font-size: 1.2rem;
        font-weight: 500;
        letter-spacing: 1px;
        width: 425px;
        box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;

        background: url("./images/bg-main-desktop.png");
        background-size: cover;
        position: relative;

        color: #fff;

        .fore_card {
            box-shadow: rgba(40, 40, 78, 0.25) 10px 20px 29px 0px;
            border-radius: 10px;
            background: url("./images/bg-card-front.png");
            position: absolute;

            top: 22%;
            left: 20%;
            width: 447px;
            height: 245px;
            padding: 35px;

            z-index: 2;

            div {
                display: block;
                letter-spacing: 2px;

                &.idunno {

                    display: flex;
                    align-items: center;

                    & * {
                        margin-right: 18px;
                    }

                    margin-bottom: 58px;
                }

                &.digit {
                    font-size: 2rem;
                    margin-bottom: 18px;

                }

                text-transform: uppercase;

                &.info {
                    display: flex;
                    font-weight: 100;
                    font-size: 1.25rem;
                    justify-content: space-between;
                }

                .avatar {
                    width: 50px;
                    height: 50px;
                    background: #fff;
                    border-radius: 50%;

                }

                .circle {
                    border: 2px solid #fff;
                    width: 25px;
                    height: 25px;
                    border-radius: 50%;
                }
            }
        }

        .back_card {
            padding: 55px;

            box-shadow: rgba(40, 40, 78, 0.25) 10px 20px 29px 0px;
            border-radius: 10px;
            width: 447px;
            height: 245px;
            position: absolute;
            bottom: 22%;
            right: -50%;
            background: url("./images/bg-card-back.png");
            display: flex;
            align-items: center;
            justify-content: right;
            font-size: 1.5rem;
            z-index: 1;
        }
    }

    .main {
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;

        form {
            width: 425px;

            &>* {
                margin-bottom: 4px;
            }

            input {
                // max-width: 500px;
                margin-bottom: 24px;
                border: 2px solid hsla(279, 6%, 55%, 0.336);
                ;

                &:focus {
                    outline: none;
                    border: 4px solid hsl(279, 36%, 56%);
                    ;

                }

                font-size: 1.25rem;
                padding: .75rem;

            }

            .complex {
                display: flex;
                justify-content: space-between;

                p {
                    margin-bottom: 4px;
                    ;
                }

                .date {
                    width: 150px;

                    input {
                        text-align: center;
                        width: 70px;
                        margin-right: 5px;
                    }
                }

                .cvc {
                    input {
                        width: 250px;
                    }
                }
            }

            display: flex;
            flex-direction: column;

            button {
                color: #fff;
                font-size: 1.5rem;
                padding: .5rem;
                background: hsl(279, 6%, 55%);
                cursor: pointer;
                border: none;

                &:hover {
                    background: hsl(279, 41%, 69%);
                }

            }
        }

    }
}

.complete {
    flex: 1;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: relative;
    iframe {
        position: absolute;
        left: 100%;
        -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
        top:30%;
        border: none;
        margin: 0;
        
    }

    
    h2 {
        margin-top: 5rem;
        font-size: 2rem;
        margin-bottom: .5rem;
    }

    button {
        margin-top: 2rem;
        width: 100%;
        color: #fff;
        letter-spacing: 1px;
        cursor: pointer;
        border-radius: 10px;
        font-size: 1.4rem;
        background: linear-gradient(90deg, hsl(249, 99%, 64%), hsl(278, 94%, 30%));
        height: 50px;
    }
}

@media (max-width:475px) {
    .App {

        .main {
            width: 300px !important;
        }
    }
}

@media (max-width:1365px) {
    .App {
        display: flex;
        flex-direction: column;
        align-items: center;

    }

    .left_bar {

        width: 100% !important;
        height: 200px;
        background: url("./images/bg-main-mobile.png") !important;
        background-repeat: no-repeat !important;
        background-size: cover !important;

        // bottom:-15%;
        .fore_card {
            left: 12vw !important;
            top: 50% !important;
        }

        .back_card {
            border-radius: 10px !important;
            right: 12vw !important;

            bottom: 0 !important;
        }
    }
}